.nav--container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  width: 120px;
  position: fixed;
  top: 0;
  left: 0;
  background-color: $col-nav-bg;
  overflow-x: hidden;
  border-right: 1px solid $col-nav;
  a {
    margin: 10px 0px;
    padding: 16px 16px;
    text-decoration: none;
    font-size: 15px;
    display: block;
    border-radius: 5px;
  }
  a:hover{
    border-radius: 5px;
    background-color: $col-hover;
    box-shadow: 5px 10px 8px #000;
  }
}

.nav-home {
  svg:hover {
    color: $col-text;
  }
}

.name {
  font-size: 1.7rem;
}

.nav-svg ul {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
